const chat = document.querySelector("#chat");
const settingsBtn = document.querySelector("#settingsBtn");
const settingsDropdown = document.querySelector("#settingsDropdown");

let width = window.innerWidth;

if (width <= 768) {
  settingsDropdown.classList.add("hide");
}

if (chat) {
  chat.scroll({ top: chat.scrollHeight, behavior: 'smooth' });
}

if (settingsBtn) {
  settingsBtn.addEventListener("click", () => {
    settingsDropdown.classList.toggle("hide");
  });
}

window.addEventListener("resize", () => {
  let width = window.innerWidth;

  if (width <= 768) {
    settingsDropdown.classList.add("hide");
  } else {
    settingsDropdown.classList.remove("hide");
  }
});

const chatResult = document.querySelector("#chatResult");

let i = 0;
setInterval(() => {
  setTimeout(() => {
    chatResult.children[i].classList.add("hide");
  
    if (i === 4) {
      i = 0;
    } else {
      i++;
    }

    chatResult.children[i].classList.remove("hide");
  }, 500);  
}, 2500);
