const menuBtn = document.querySelector("#menuBtn");
const closeBtn = document.querySelector("#closeBtn");

const navBtns = document.querySelectorAll(".nav__btns > .nav__btn");

const navBtnsTransform = [
  "translate3d(0, 412px, 0)",
  "translate3d(0, 314px, 0)",
  "translate3d(0, 216px, 0)",
  "translate3d(0, 118px, 0)",
];

if (menuBtn) {
  menuBtn.addEventListener("click", () => {
    if (!closeBtn.classList.contains("show")) {
      closeBtn.classList.remove("hide");
      closeBtn.classList.add("show");
    }

    if (!menuBtn.classList.contains("hide")) {
      menuBtn.classList.remove("show");
      menuBtn.classList.add("hide");
    }

    navBtns.forEach((navBtn) => {
      navBtn.style.opacity = 1;
      navBtn.style.transform = "translate3d(0, 0, 0)";
    });
  });
}

if (closeBtn) {
  closeBtn.addEventListener("click", () => {
    if (!closeBtn.classList.contains("hide")) {
      closeBtn.classList.remove("show");
      closeBtn.classList.add("hide");
    }

    if (!menuBtn.classList.contains("show")) {
      menuBtn.classList.remove("hide");
      menuBtn.classList.add("show");
    }

    navBtns.forEach((navBtn, index) => {
      navBtn.style.opacity = 0;
      navBtn.style.transform = navBtnsTransform[index];
    });
  });
}

const handleNavBtnClick = (e) => {
  const target = e.target;
  const children = target.children;

  navBtns.forEach(btn => {
    let children = btn.children;
    children[0].classList.remove("hide");
    children[1].classList.add("hide");
  });

  children[0].classList.toggle("hide")
  children[1].classList.toggle("hide")
};

navBtns.forEach((btn) => {
  btn.addEventListener("click", handleNavBtnClick);
});

window.addEventListener("resize", () => {
  let width = window.innerWidth;
  let height = window.innerHeight;

  if (width > 768) {
      navBtns.forEach((navBtn, index) => {
        navBtn.style.opacity = 0;
        navBtn.style.transform = navBtnsTransform[index];
      });
  
      closeBtn.classList.remove("show");
      menuBtn.classList.remove("hide");

      if (width > height && width <= 812) {
        navBtns.forEach((navBtn) => {
          navBtn.style.opacity = 1;
          navBtn.style.transform = "translate3d(0, 0, 0)";
        });
      }
  } else {
    navBtns.forEach((navBtn) => {
      navBtn.style.opacity = 1;
      navBtn.style.transform = "translate3d(0, 0, 0)";
    });

    if (width > height && width <= 812) {
      navBtns.forEach((navBtn) => {
        navBtn.style.opacity = 1;
        navBtn.style.transform = "translate3d(0, 0, 0)";
      });
    }
  }
  }
);
