const modals = document.querySelectorAll(".modal");
const container = document.querySelector(".page-container");

let width = window.innerWidth;
let height = window.innerHeight;

var active = false;
var currentX;
var currentY;
var initialX;
var initialY;
var xOffset = 0;
var yOffset = 0;

function dragStart(e) {
  if (e.type === "touchstart") {
    initialX = e.touches[0].clientX - xOffset;
    initialY = e.touches[0].clientY - yOffset;
  } else {
    initialX = e.clientX - xOffset;
    initialY = e.clientY - yOffset;
  }

  modals.forEach((modal) => {
    if (e.target === modal) {
      active = true;
    }
  });
}

function dragEnd(e) {
  initialX = currentX;
  initialY = currentY;

  active = false;
}

function drag(e) {
  if (active) {
    e.preventDefault();

    if (e.type === "touchmove") {
      currentX = e.touches[0].clientX - initialX;
      currentY = e.touches[0].clientY - initialY;
    } else {
      currentX = e.clientX - initialX;
      currentY = e.clientY - initialY;
    }

    xOffset = currentX;
    yOffset = currentY;


    modals.forEach((modal) => {
      setTranslate(currentX, currentY, modal);
    });
  }
}

function setTranslate(xPos, yPos, el) {
  el.style.transform = "translate3d(" + xPos + "px, " + yPos + "px, 0)";
  el.style.position = "fixed";
  el.style.zIndex = 200;
}

const resetModalsTranslate = () => {
  modals.forEach((modal) => {
    modal.style.transform = "translate3d(0, 0, 0)";
    modal.style.position = "static";
    modal.style.zIndex = 1;
  });
};

const addEvents = (width, height) => {
  if (width <= 768 || (width <= 812 && width > height)) {
    container.removeEventListener("touchstart", dragStart, false);
    container.removeEventListener("touchend", dragEnd, false);
    container.removeEventListener("touchmove", drag, false);

    container.removeEventListener("mousedown", dragStart, false);
    container.removeEventListener("mouseup", dragEnd, false);
    container.removeEventListener("mousemove", drag, false);

    resetModalsTranslate();
  } else {
    container.addEventListener("touchstart", dragStart, false);
    container.addEventListener("touchend", dragEnd, false);
    container.addEventListener("touchmove", drag, false);

    container.addEventListener("mousedown", dragStart, false);
    container.addEventListener("mouseup", dragEnd, false);
    container.addEventListener("mousemove", drag, false);
  }
};

addEvents();

window.addEventListener("resize", () => {
  let width = window.innerWidth;
  let height = window.innerHeight;
  addEvents(width, height)
});
