const ratingInputs = document.querySelectorAll(".rating__input");
const ratingLabels = document.querySelectorAll(".rating__star");

const ratingModal = document.querySelector("#rating");
const ratingConfirmationMsgModal = document.querySelector("#ratingConfirmationMsgModal");

const STARS = {
  empty: 'assets/starEmpty.svg',
  filled: 'assets/starFilled.svg',
};

if (ratingInputs) {
  ratingInputs.forEach((ratingInput) => {
    ratingInput.addEventListener('click', (e) => {
      let starValue = e.target.value;
      generateStars(starValue);

      setTimeout(() => {
        ratingModal.classList.add("modal--hide");
        ratingConfirmationMsgModal.classList.remove("modal--hide");
      }, 500);
    });
  });
}

const generateStars = (value) => {
  let imgElem;
  let currentImg;

  for (let i = 0; i < 5; i++) {
    imgElem = document.createElement("img");

    if ((i + 1) <= value) {
      imgElem.src = STARS.filled;
      imgElem.alt = "Filled Star";
    } else {
      imgElem.src = STARS.empty;
      imgElem.alt = "Empty Star";
    }

    currentImg = ratingInputs[i].nextElementSibling;
    currentImg.remove();
    ratingLabels[i].appendChild(imgElem);
  }
};
