const TEST_SEARCH = [
  "Happy holidays",
  "Happymood",
  "Happy birthday",
  "Nice",
  "Life",
  "Life hacks"
];

let showList = false;
let resultItems = [];

const searchInputContainer = document.querySelector("#searchInputContainer");
const searchInput = document.querySelector("#searchInput");
const autocompleteListContainer = document.querySelector(".search__autocomplete-container");

const handleSearchAutocomplete = (e) => {
  const target = e.target;
  let value = target.value;

  if (!value) { 
    autocompleteListContainer.style.display = "none";
    searchInput.classList.remove("search__input--show-autocomplete");
    return false;
  }

  const autocompleteList = document.createElement("ul");

  removeList(autocompleteListContainer);

  TEST_SEARCH.forEach((result) => {
    if (result.substr(0, value.length).toLowerCase() == value.toLowerCase()) {
      resultItems.push(result);
    }
  });

  if (resultItems.length > 0) {
    searchInput.classList.add("search__input--show-autocomplete");

    resultItems.forEach(itemContent => {
      let item = document.createElement("li");
      let itemLink = document.createElement("a");

      itemLink.innerHTML = itemContent;
      itemLink.classList.add("search__link");
      item.appendChild(itemLink);
      autocompleteList.appendChild(item);
    });

    autocompleteListContainer.appendChild(autocompleteList);
    autocompleteListContainer.style.display = "block";
  } else {
    searchInput.classList.remove("search__input--show-autocomplete");

    removeList(autocompleteListContainer);
    autocompleteListContainer.style.display = "none";
  }

  resultItems = []
};

const removeList = (listContainer) => {
  let children = listContainer.children;
  if (children.length > 0) {
    listContainer.removeChild(children[0]);
  }
}

searchInput.addEventListener("input", handleSearchAutocomplete);
