const fileInputDragArea = document.querySelector("#fileInputDragArea");
const filesList = document.querySelector("#filesList");

if (fileInputDragArea) {
  ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
    fileInputDragArea.addEventListener(eventName, preventDefaults, false);
  })
  
  function preventDefaults (e) {
    e.preventDefault()
    e.stopPropagation()
  }
  
  ;['dragenter', 'dragover'].forEach(eventName => {
    fileInputDragArea.addEventListener(eventName, highlight, false)
  })
  
  ;['dragleave', 'drop'].forEach(eventName => {
    fileInputDragArea.addEventListener(eventName, unhighlight, false)
  })
  
  function highlight() {
    fileInputDragArea.classList.add('custom-file-input--highlight')
  }
  
  function unhighlight() {
    fileInputDragArea.classList.remove('custom-file-input--highlight')
  }
  
  fileInputDragArea.addEventListener('drop', handleDrop, false);
  
  function handleDrop(e) {
    let dt = e.dataTransfer;
    let files = dt.files;
  
    handleFiles(files);
  }
  
  const handleFiles = (files) => {
    if (files && files.length > 0) {
      // ...
    }
  }
}
